"use client"

import type React from "react"

import { motion } from "framer-motion"
import { AlertCircle, Clock, CheckCircle, TrendingUp } from "lucide-react"
import { useRequests } from "@/store/requests-provider"

export default function StatsOverlay() {
  const { requests } = useRequests()

  const stats = {
    new: requests.filter((r) => r.status === "new").length,
    inProgress: requests.filter((r) => r.status === "in_progress").length,
    done: requests.filter((r) => r.status === "done").length,
    total: requests.length,
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: -20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: 0.3 }}
      className="fixed top-4 left-1/2 -translate-x-1/2 z-[1000] hidden lg:block"
    >
      <div className="glass-strong rounded-2xl p-3 shadow-2xl neon-glow-primary">
        <div className="flex items-center gap-6">
          <StatItem icon={<TrendingUp className="h-4 w-4" />} label="–í—Å–µ–≥–æ" value={stats.total} color="text-primary" />
          <div className="w-px h-8 bg-border/50" />
          <StatItem icon={<AlertCircle className="h-4 w-4" />} label="–ù–æ–≤—ã–µ" value={stats.new} color="text-blue-400" />
          <div className="w-px h-8 bg-border/50" />
          <StatItem
            icon={<Clock className="h-4 w-4" />}
            label="–í —Ä–∞–±–æ—Ç–µ"
            value={stats.inProgress}
            color="text-yellow-400"
          />
          <div className="w-px h-8 bg-border/50" />
          <StatItem
            icon={<CheckCircle className="h-4 w-4" />}
            label="–í—ã–ø–æ–ª–Ω–µ–Ω—ã"
            value={stats.done}
            color="text-green-400"
          />
        </div>
      </div>
    </motion.div>
  )
}

function StatItem({
  icon,
  label,
  value,
  color,
}: {
  icon: React.ReactNode
  label: string
  value: number
  color: string
}) {
  return (
    <div className="flex items-center gap-2">
      <div className={`${color}`}>{icon}</div>
      <div>
        <div className="text-xs text-muted-foreground">{label}</div>
        <div className={`text-lg font-bold ${color}`}>{value}</div>
      </div>
    </div>
  )
}
