"use client"

import { useState, useEffect } from "react"
import dynamic from "next/dynamic"
import RequestPanel from "./request-panel"
import GeminiChat from "./gemini-chat"
import ThemeToggle from "./theme-toggle"
import StatsOverlay from "./stats-overlay"
import Loader from "./loader"
import { Button } from "./ui/button"
import { Plus } from "lucide-react"
import { motion, AnimatePresence } from "framer-motion"

const Map = dynamic(() => import("./map"), {
  ssr: false,
  loading: () => <Loader />,
})

export default function MainLayout() {
  const [isChatOpen, setIsChatOpen] = useState(false)
  const [isPanelOpen, setIsPanelOpen] = useState(true)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const timer = setTimeout(() => setIsLoading(false), 1500)
    return () => clearTimeout(timer)
  }, [])

  if (isLoading) {
    return <Loader />
  }

  return (
    <div className="h-screen w-full overflow-hidden relative">
      {/* Map */}
      <div className="absolute inset-0">
        <Map />
      </div>

      {/* Stats Overlay */}
      <StatsOverlay />

      {/* Theme Toggle */}
      <div className="absolute top-4 right-4 z-[1000]">
        <ThemeToggle />
      </div>

      {/* Request Panel */}
      <RequestPanel isOpen={isPanelOpen} onToggle={() => setIsPanelOpen(!isPanelOpen)} />

      {/* Submit Request Button */}
      <AnimatePresence>
        {!isChatOpen && (
          <motion.div
            initial={{ opacity: 0, scale: 0.8 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.8 }}
            transition={{ type: "spring", damping: 20 }}
            className="fixed bottom-6 right-6 z-[1000] md:bottom-8 md:right-8"
          >
            <Button
              size="lg"
              onClick={() => setIsChatOpen(true)}
              className="glass neon-glow-primary rounded-full h-16 px-8 text-base font-semibold shadow-2xl hover:scale-110 transition-transform bg-primary/20 hover:bg-primary/30 text-primary border-2 border-primary/50"
            >
              <Plus className="mr-2 h-6 w-6" />
              –ü–æ–¥–∞—Ç—å –∑–∞—è–≤–∫—É
            </Button>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Gemini Chat */}
      <GeminiChat isOpen={isChatOpen} onClose={() => setIsChatOpen(false)} />
    </div>
  )
}
