"use client"

import { motion } from "framer-motion"
import { MapPin, Calendar, Tag } from "lucide-react"
import type { Request } from "@/store/requests-provider"
import { getCategoryInfo } from "@/lib/categories"
import { formatDistanceToNow } from "date-fns"
import { ru } from "date-fns/locale"

interface RequestCardProps {
  request: Request
}

export default function RequestCard({ request }: RequestCardProps) {
  const categoryInfo = getCategoryInfo(request.category)

  const statusStyles = {
    new: "bg-blue-500/20 text-blue-400 border-blue-500/30",
    in_progress: "bg-yellow-500/20 text-yellow-400 border-yellow-500/30",
    done: "bg-green-500/20 text-green-400 border-green-500/30",
  }

  const statusLabels = {
    new: "–ù–æ–≤–∞—è",
    in_progress: "–í —Ä–∞–±–æ—Ç–µ",
    done: "–í—ã–ø–æ–ª–Ω–µ–Ω–∞",
  }

  return (
    <motion.div
      whileHover={{ scale: 1.02 }}
      whileTap={{ scale: 0.98 }}
      className={`glass rounded-xl p-4 border border-border/50 cursor-pointer hover:neon-glow-primary transition-all ${
        request.status === "new" ? "animate-pulse-glow" : ""
      }`}
    >
      <div className="flex items-start justify-between gap-2 mb-2">
        <h3 className="font-semibold text-sm leading-tight line-clamp-2 flex-1">{request.title}</h3>
        <span className="text-xl shrink-0">{categoryInfo.icon}</span>
      </div>

      <p className="text-xs text-muted-foreground mb-3 line-clamp-2 leading-relaxed">{request.description}</p>

      <div className="flex items-center gap-2 mb-2">
        <MapPin className="h-3 w-3 text-muted-foreground shrink-0" />
        <span className="text-xs text-foreground line-clamp-1">{request.address}</span>
      </div>

      <div className="flex items-center justify-between gap-2">
        <div className="flex items-center gap-1">
          <Calendar className="h-3 w-3 text-muted-foreground" />
          <span className="text-xs text-muted-foreground">
            {formatDistanceToNow(new Date(request.createdAt), { addSuffix: true, locale: ru })}
          </span>
        </div>

        <span className={`px-2 py-1 rounded-full text-xs border ${statusStyles[request.status]} font-medium shrink-0`}>
          {statusLabels[request.status]}
        </span>
      </div>

      <div className="mt-2 pt-2 border-t border-border/30">
        <div className="flex items-center gap-1">
          <Tag className="h-3 w-3 text-muted-foreground" />
          <span className="text-xs text-muted-foreground">{categoryInfo.label}</span>
        </div>
      </div>
    </motion.div>
  )
}
