"use server"

import { GoogleGenerativeAI } from "@google/generative-ai"

const genAI = new GoogleGenerativeAI(process.env.GEMINI_API_KEY || "")

interface RequestData {
  title?: string
  description?: string
  category?: string
  address?: string
  coordinates?: [number, number]
}

interface GeminiResponse {
  message: string
  requestData?: Partial<RequestData>
  isComplete?: boolean
}

export async function chatWithGemini(conversationHistory: string, currentData: RequestData): Promise<GeminiResponse> {
  try {
    const model = genAI.getGenerativeModel({ model: "gemini-1.5-flash" })

    const prompt = `–¢—ã - –ø–æ–º–æ—â–Ω–∏–∫ –¥–ª—è –ø–æ–¥–∞—á–∏ –∑–∞—è–≤–æ–∫ –≤ –ö–°–ö (–∫–æ–æ–ø–µ—Ä–∞—Ç–∏–≤ —Å–æ–±—Å—Ç–≤–µ–Ω–Ω–∏–∫–æ–≤ –∫–≤–∞—Ä—Ç–∏—Ä).
–¢–≤–æ—è –∑–∞–¥–∞—á–∞ - —Å–æ–±—Ä–∞—Ç—å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –æ –ø—Ä–æ–±–ª–µ–º–µ –∂–∏–ª—å—Ü–∞ –∏ –ø–æ–º–æ—á—å —Å—Ñ–æ—Ä–º–∏—Ä–æ–≤–∞—Ç—å –∑–∞—è–≤–∫—É.

–¢–µ–∫—É—â–∏–µ –¥–∞–Ω–Ω—ã–µ –∑–∞—è–≤–∫–∏:
${JSON.stringify(currentData, null, 2)}

–ò—Å—Ç–æ—Ä–∏—è —Ä–∞–∑–≥–æ–≤–æ—Ä–∞:
${conversationHistory}

–ó–∞–¥–∞—á–∏:
1. –û–ø—Ä–µ–¥–µ–ª–∏ –∫–∞—Ç–µ–≥–æ—Ä–∏—é –ø—Ä–æ–±–ª–µ–º—ã (emergency, cleaning, lighting, lift, heating, repair, parking, playground, noise, other)
2. –ü–æ–ª—É—á–∏ –æ–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ–±–ª–µ–º—ã
3. –ü–æ–ª—É—á–∏ –∞–¥—Ä–µ—Å –∏–ª–∏ –ø—Ä–µ–¥–ª–æ–∂–∏ —É–∫–∞–∑–∞—Ç—å –Ω–∞ –∫–∞—Ä—Ç–µ
4. –°—Ñ–æ—Ä–º–∏—Ä—É–π –∫—Ä–∞—Ç–∫–∏–π –∑–∞–≥–æ–ª–æ–≤–æ–∫ –∑–∞—è–≤–∫–∏ (–¥–æ 50 —Å–∏–º–≤–æ–ª–æ–≤)

–ï—Å–ª–∏ –≤—Å–µ –¥–∞–Ω–Ω—ã–µ —Å–æ–±—Ä–∞–Ω—ã (–∫–∞—Ç–µ–≥–æ—Ä–∏—è, –æ–ø–∏—Å–∞–Ω–∏–µ, –∞–¥—Ä–µ—Å/–∫–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã), –æ—Ç–≤–µ—Ç—å "COMPLETE" –≤ –∫–æ–Ω—Ü–µ —Å–æ–æ–±—â–µ–Ω–∏—è.

–û—Ç–≤–µ—Ç—å –Ω–∞ —Ä—É—Å—Å–∫–æ–º —è–∑—ã–∫–µ, –¥—Ä—É–∂–µ–ª—é–±–Ω–æ –∏ –∫—Ä–∞—Ç–∫–æ. –ò—Å–ø–æ–ª—å–∑—É–π —Ñ–æ—Ä–º–∞—Ç JSON –¥–ª—è –∏–∑–≤–ª–µ—á–µ–Ω–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö.

–§–æ—Ä–º–∞—Ç –æ—Ç–≤–µ—Ç–∞:
{
  "message": "—Ç–≤–æ–π –æ—Ç–≤–µ—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é",
  "requestData": {
    "title": "–∫—Ä–∞—Ç–∫–∏–π –∑–∞–≥–æ–ª–æ–≤–æ–∫ –∏–ª–∏ null",
    "description": "–æ–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ–±–ª–µ–º—ã –∏–ª–∏ null",
    "category": "–∫–∞—Ç–µ–≥–æ—Ä–∏—è –∏–ª–∏ null",
    "address": "–∞–¥—Ä–µ—Å –∏–ª–∏ null"
  },
  "isComplete": false
}`

    const result = await model.generateContent(prompt)
    const response = result.response.text()

    try {
      const parsed = JSON.parse(response)
      return parsed as GeminiResponse
    } catch {
      return {
        message: response,
        requestData: {},
        isComplete: false,
      }
    }
  } catch (error) {
    console.error("[v0] Gemini API Error:", error)
    return {
      message: "–ö —Å–æ–∂–∞–ª–µ–Ω–∏—é, —è –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –∑–∞–ø–æ–ª–Ω–∏—Ç–µ –∑–∞—è–≤–∫—É –≤—Ä—É—á–Ω—É—é.",
      requestData: {},
      isComplete: false,
    }
  }
}
