"use client"

import { useState, useRef, useEffect } from "react"
import { motion, AnimatePresence } from "framer-motion"
import { X, Send, MapPin, ImageIcon, Loader2 } from "lucide-react"
import { Button } from "./ui/button"
import { Input } from "./ui/input"
import { ScrollArea } from "./ui/scroll-area"
import { categories } from "@/lib/categories"
import { useRequests } from "@/store/requests-provider"
import { chatWithGemini } from "@/lib/gemini"
import MapSelector from "./map-selector"

interface Message {
  id: string
  role: "user" | "assistant"
  content: string
  timestamp: Date
}

interface RequestData {
  title?: string
  description?: string
  category?: string
  address?: string
  coordinates?: [number, number]
}

interface GeminiChatProps {
  isOpen: boolean
  onClose: () => void
}

export default function GeminiChat({ isOpen, onClose }: GeminiChatProps) {
  const [messages, setMessages] = useState<Message[]>([])
  const [input, setInput] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const [showMapSelector, setShowMapSelector] = useState(false)
  const [requestData, setRequestData] = useState<RequestData>({})
  const scrollRef = useRef<HTMLDivElement>(null)
  const { addRequest } = useRequests()

  useEffect(() => {
    if (isOpen && messages.length === 0) {
      const welcomeMessage: Message = {
        id: "welcome",
        role: "assistant",
        content: "–ó–¥—Ä–∞–≤—Å—Ç–≤—É–π—Ç–µ! –Ø –ø–æ–º–æ–≥—É –≤–∞–º –ø–æ–¥–∞—Ç—å –∑–∞—è–≤–∫—É. –†–∞—Å—Å–∫–∞–∂–∏—Ç–µ, –ø–æ–∂–∞–ª—É–π—Å—Ç–∞, –∫–∞–∫–∞—è —É –≤–∞—Å –ø—Ä–æ–±–ª–µ–º–∞?",
        timestamp: new Date(),
      }
      setMessages([welcomeMessage])
    }
  }, [isOpen, messages.length])

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight
    }
  }, [messages])

  const handleSend = async () => {
    if (!input.trim() || isLoading) return

    const userMessage: Message = {
      id: Date.now().toString(),
      role: "user",
      content: input,
      timestamp: new Date(),
    }

    setMessages((prev) => [...prev, userMessage])
    setInput("")
    setIsLoading(true)

    try {
      const conversationHistory = [...messages, userMessage]
        .map((m) => `${m.role === "user" ? "–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å" : "–ê—Å—Å–∏—Å—Ç–µ–Ω—Ç"}: ${m.content}`)
        .join("\n")

      const response = await chatWithGemini(conversationHistory, requestData)

      const assistantMessage: Message = {
        id: (Date.now() + 1).toString(),
        role: "assistant",
        content: response.message,
        timestamp: new Date(),
      }

      setMessages((prev) => [...prev, assistantMessage])

      if (response.requestData) {
        setRequestData((prev) => ({ ...prev, ...response.requestData }))
      }

      if (response.isComplete && response.requestData) {
        setTimeout(() => {
          submitRequest(response.requestData!)
        }, 1500)
      }
    } catch (error) {
      console.error("[v0] Error sending message:", error)
      const errorMessage: Message = {
        id: (Date.now() + 1).toString(),
        role: "assistant",
        content: "–ò–∑–≤–∏–Ω–∏—Ç–µ, –ø—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑.",
        timestamp: new Date(),
      }
      setMessages((prev) => [...prev, errorMessage])
    } finally {
      setIsLoading(false)
    }
  }

  const handleCategorySelect = (categoryId: string) => {
    setRequestData((prev) => ({ ...prev, category: categoryId }))
    const category = categories.find((c) => c.id === categoryId)
    const message: Message = {
      id: Date.now().toString(),
      role: "user",
      content: `–ö–∞—Ç–µ–≥–æ—Ä–∏—è: ${category?.label}`,
      timestamp: new Date(),
    }
    setMessages((prev) => [...prev, message])

    setTimeout(() => {
      const response: Message = {
        id: (Date.now() + 1).toString(),
        role: "assistant",
        content: "–û—Ç–ª–∏—á–Ω–æ! –¢–µ–ø–µ—Ä—å —Ä–∞—Å—Å–∫–∞–∂–∏—Ç–µ –ø–æ–¥—Ä–æ–±–Ω–µ–µ –æ –ø—Ä–æ–±–ª–µ–º–µ –∏ —É–∫–∞–∂–∏—Ç–µ –∞–¥—Ä–µ—Å.",
        timestamp: new Date(),
      }
      setMessages((prev) => [...prev, response])
    }, 500)
  }

  const handleMapSelect = (coords: [number, number], address: string) => {
    setRequestData((prev) => ({ ...prev, coordinates: coords, address }))
    setShowMapSelector(false)
    const message: Message = {
      id: Date.now().toString(),
      role: "user",
      content: `–ú–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ: ${address}`,
      timestamp: new Date(),
    }
    setMessages((prev) => [...prev, message])
  }

  const submitRequest = (data: RequestData) => {
    if (!data.title || !data.description || !data.category || !data.coordinates || !data.address) {
      console.error("[v0] Missing request data", data)
      return
    }

    addRequest({
      title: data.title,
      description: data.description,
      category: data.category,
      status: "new",
      coordinates: data.coordinates,
      address: data.address,
      author: "–ñ–∏–ª–µ—Ü",
    })

    const successMessage: Message = {
      id: Date.now().toString(),
      role: "assistant",
      content: "–ó–∞—è–≤–∫–∞ —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∞! –í—ã –º–æ–∂–µ—Ç–µ —É–≤–∏–¥–µ—Ç—å –µ—ë –Ω–∞ –∫–∞—Ä—Ç–µ. –°–ø–∞—Å–∏–±–æ!",
      timestamp: new Date(),
    }
    setMessages((prev) => [...prev, successMessage])

    setTimeout(() => {
      handleClose()
    }, 2000)
  }

  const handleClose = () => {
    setMessages([])
    setRequestData({})
    onClose()
  }

  const showCategoryButtons =
    !requestData.category && messages.length > 1 && messages[messages.length - 1].role === "assistant"

  return (
    <>
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 z-[2000] flex items-center justify-center p-4 bg-background/80 backdrop-blur-sm"
          >
            <motion.div
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              transition={{ type: "spring", damping: 25 }}
              className="w-full max-w-2xl h-[80vh] glass-strong rounded-2xl shadow-2xl neon-glow-primary flex flex-col overflow-hidden"
            >
              {/* Header */}
              <div className="flex items-center justify-between p-4 border-b border-border/50">
                <h2 className="text-xl font-semibold text-glow-primary">–ü–æ–¥–∞—Ç—å –∑–∞—è–≤–∫—É</h2>
                <Button variant="ghost" size="icon" onClick={handleClose} className="hover:bg-muted/50">
                  <X className="h-5 w-5" />
                </Button>
              </div>

              {/* Messages */}
              <ScrollArea className="flex-1 p-4" ref={scrollRef}>
                <div className="space-y-4">
                  {messages.map((message) => (
                    <motion.div
                      key={message.id}
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      className={`flex ${message.role === "user" ? "justify-end" : "justify-start"}`}
                    >
                      <div
                        className={`max-w-[80%] p-3 rounded-2xl ${
                          message.role === "user"
                            ? "bg-primary text-primary-foreground neon-glow-primary"
                            : "glass border border-border/50"
                        }`}
                      >
                        <p className="text-sm leading-relaxed">{message.content}</p>
                      </div>
                    </motion.div>
                  ))}

                  {showCategoryButtons && (
                    <motion.div
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      className="grid grid-cols-2 gap-2"
                    >
                      {categories.slice(0, 6).map((category) => (
                        <Button
                          key={category.id}
                          variant="outline"
                          onClick={() => handleCategorySelect(category.id)}
                          className="glass hover:neon-glow-primary transition-all justify-start h-auto py-3"
                        >
                          <span className="mr-2 text-lg">{category.icon}</span>
                          <span className="text-sm">{category.label}</span>
                        </Button>
                      ))}
                    </motion.div>
                  )}

                  {isLoading && (
                    <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="flex justify-start">
                      <div className="glass border border-border/50 p-3 rounded-2xl">
                        <Loader2 className="h-5 w-5 animate-spin text-primary" />
                      </div>
                    </motion.div>
                  )}
                </div>
              </ScrollArea>

              {/* Input */}
              <div className="p-4 border-t border-border/50 space-y-2">
                <div className="flex gap-2">
                  <Button
                    variant="outline"
                    size="icon"
                    onClick={() => setShowMapSelector(true)}
                    className="glass hover:neon-glow-primary shrink-0"
                    disabled={isLoading}
                  >
                    <MapPin className="h-5 w-5" />
                  </Button>
                  <Button
                    variant="outline"
                    size="icon"
                    className="glass hover:neon-glow-primary shrink-0 bg-transparent"
                    disabled={true}
                  >
                    <ImageIcon className="h-5 w-5" />
                  </Button>
                  <Input
                    value={input}
                    onChange={(e) => setInput(e.target.value)}
                    onKeyDown={(e) => e.key === "Enter" && !e.shiftKey && handleSend()}
                    placeholder="–í–≤–µ–¥–∏—Ç–µ —Å–æ–æ–±—â–µ–Ω–∏–µ..."
                    className="glass border-border/50 focus:neon-glow-primary"
                    disabled={isLoading}
                  />
                  <Button
                    onClick={handleSend}
                    disabled={!input.trim() || isLoading}
                    className="shrink-0 neon-glow-primary"
                  >
                    <Send className="h-5 w-5" />
                  </Button>
                </div>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>

      <MapSelector isOpen={showMapSelector} onClose={() => setShowMapSelector(false)} onSelect={handleMapSelect} />
    </>
  )
}
